import { useCallback, useState } from "react";
import "./styles.css";
import Expenses from "./Expenses";
import NewExpense from "./Expenses/NewExpense";

const Main = () => {
  const [showForm, setShowForm] = useState(false);
  const [newExpenseAddedAt, setNewExpenseAddedAt] = useState(null);
  const [formValues, setFormValues] = useState(null);

  const openForm = useCallback(() => {
    setShowForm(true);
  }, []);

  const closeForm = useCallback(() => {
    setFormValues(null);
    setShowForm(false);
  }, []);

  return (
    <>
      {(showForm || formValues !== null) && (
        <NewExpense
          closeForm={closeForm}
          setNewExpenseAddedAt={setNewExpenseAddedAt}
          formValues={formValues}
        />
      )}

      <div className={showForm || formValues !== null ? "invisible" : ""}>
        <Expenses
          openForm={openForm}
          setFormValues={setFormValues}
          key={newExpenseAddedAt}
        />
      </div>
    </>
  );
};

export default function App() {
  return <Main />;
}
