import "../styles.css";
import DeleteIcon from "./DeleteIcon";
import VisibilityIcon from "./VisibilityIcon";
import EditIcon from "./EditIcon";

function formatDate(timestamp) {
  const d = new Date(timestamp);
  return d.toLocaleString();
}

const ExpenseList = ({ expenses, setFormValues }) => {
  let total = 0;
  let myExpense = 0;
  let sharedExpense = 0;

  return (
    <div className="container">
      <table className="table table-sm">
        <thead>
          <tr>
            <th scope="col">Date</th>
            <th scope="col">Title</th>
            <th scope="col">Amount</th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          {expenses.map((item, i) => {
            total += parseFloat(item.amount);
            if (item.private) {
              myExpense += parseFloat(item.amount);
            } else {
              sharedExpense += parseFloat(item.amount);
            }

            return (
              <tr key={i}>
                <td>{formatDate(item.timestamp)}</td>
                <td>{item.name}</td>
                <td>{item.amount}</td>
                <td>
                  <VisibilityIcon id={item.id} isPrivate={item.private} />
                  &nbsp;&nbsp;&nbsp;&nbsp;
                  <EditIcon item={item} setFormValues={setFormValues} />
                  &nbsp;&nbsp;&nbsp;&nbsp;
                  <DeleteIcon id={item.id} />
                </td>
              </tr>
            );
          })}
        </tbody>
        <tfoot>
          <tr>
            <th scope="col" colSpan={1}></th>
            <th scope="col">Shared Expense</th>
            <th scope="col">{sharedExpense}</th>
            <th scope="col"></th>
          </tr>
          <tr>
            <th scope="col" colSpan={1}></th>
            <th scope="col">My Expense</th>
            <th scope="col">{myExpense}</th>
            <th scope="col"></th>
          </tr>
          <tr>
            <th scope="col" colSpan={1}></th>
            <th scope="col">Total</th>
            <th scope="col">{total}</th>
            <th scope="col"></th>
          </tr>
        </tfoot>
      </table>
    </div>
  );
};

export default ExpenseList;
