import { useCallback, useEffect, useState } from "react";

const NewExpense = ({ closeForm, setNewExpenseAddedAt, formValues }) => {
  const isEdit = formValues !== null;
  const [data, setData] = useState({
    name: "",
    amount: "",
    private: true
  });

  useEffect(() => {
    if (formValues !== null) {
      setData(formValues);
    }
  }, [formValues]);

  const setField = useCallback((e) => {
    setData((p) => ({
      ...p,
      [e.target.name]: e.target.value
    }));
  }, []);

  const setCheckBox = useCallback((e) => {
    setData((p) => ({
      ...p,
      private: p.private === true ? false : true
    }));
  }, []);

  const addExpense = useCallback(() => {
    // post data to server
    console.log(data);
    if (data.name && data.amount) {
      setNewExpenseAddedAt(new Date().getTime());
      closeForm();
    } else {
      window.alert("Fields missing");
    }
  }, [setNewExpenseAddedAt, closeForm, data]);

  return (
    <div className="container">
      {/* header */}
      <div className="row my-3">
        <div className="col"></div>
        <div className="col-6">
          <h5 className="text-center ">Add New</h5>
        </div>
        <div className="col d-flex justify-content-end align-items-center">
          <button
            type="button"
            className="btn btn-warning btn-sm"
            onClick={closeForm}
          >
            Close
          </button>
        </div>
      </div>

      {/* form */}
      <form className="row g-3">
        <div className="col-md-12">
          <div className="form-check form-switch">
            <input
              className="form-check-input"
              type="checkbox"
              id="flexSwitchCheckDefault"
              onChange={setCheckBox}
              name="private"
              checked={data.private === false}
            />
            <label
              className="form-check-label"
              htmlFor="flexSwitchCheckDefault"
            >
              Shared Expense
            </label>
          </div>
        </div>
        <div className="col-md-12">
          <label htmlFor="inputEmail4" className="form-label">
            Expense Name
          </label>
          <input
            className="form-control form-control-sm"
            id="inputEmail4"
            onChange={setField}
            name="name"
            value={data.name}
          />
        </div>
        <div className="col-md-12">
          <label htmlFor="inputPassword4" className="form-label">
            Amount
          </label>

          <div className="input-group input-group-sm  mb-3">
            <span className="input-group-text" id="inputGroup-sizing-default">
              AED
            </span>
            <input
              type="text"
              className="form-control"
              aria-label="Sizing example input"
              aria-describedby="inputGroup-sizing-default"
              onChange={setField}
              name="amount"
              value={data.amount}
            />
          </div>
        </div>

        <div className="col-12">
          <button
            type="submit"
            className="btn btn-primary"
            onClick={addExpense}
          >
            {formValues !== null ? "Update" : "Add"}
          </button>
        </div>
      </form>
    </div>
  );
};

export default NewExpense;
