import { useCallback } from "react";
import { FiEdit2 } from "react-icons/fi";

const EditIcon = ({ item, setFormValues }) => {
  const editItem = useCallback(() => {
    setFormValues(item);
  }, [item, setFormValues]);

  return <FiEdit2 onClick={editItem} color="blue" size={14} />;
};

export default EditIcon;
