import "../styles.css";
import { useCallback, useEffect, useState } from "react";
import ExpenseList from "./ExpenseList";

const listDummy = [
  {
    id: 1,
    name: "Al Maya",
    amount: "55.7",
    currency: "AED",
    timestamp: 1633440836004,
    private: true,
    img: null
  },
  {
    id: 2,
    name: "Lulu Hypermarket",
    amount: "123.45",
    currency: "AED",
    timestamp: 1633440853638,
    private: false,
    img: null
  }
];

const Expenses = ({ openForm, setFormValues }) => {
  const [refreshing, setRefreshing] = useState(true);
  const [expenses, setExpenses] = useState([]);

  const getItems = useCallback(() => {
    // get data from server
    console.log("Calling API");
    setRefreshing(true);
    setTimeout(function () {
      setExpenses(listDummy);
      setRefreshing(false);
    }, 1000);
  }, []);

  useEffect(() => {
    getItems();
  }, [getItems]);

  return (
    <div className="container">
      {/* header */}
      <div className="row my-3">
        <div className="col">
          <button
            type="button"
            className="btn btn-success btn-sm"
            onClick={openForm}
          >
            Add
          </button>
        </div>
        <div className="col d-flex align-items-center">
          <h5 className="text-center ">Expenses</h5>
        </div>
        <div className="col d-flex justify-content-end align-items-center">
          <button
            type="button"
            className="btn btn-warning btn-sm"
            onClick={getItems}
          >
            Refresh
          </button>
        </div>
      </div>

      <div className="row">
        {/* loader */}
        {refreshing && (
          <div className="col d-flex justify-content-center">
            <div className="spinner-border text-primary " role="status">
              <span className="visually-hidden">Loading...</span>
            </div>
          </div>
        )}

        {/* empty component */}
        {!refreshing && expenses.length === 0 && <div>No Items to Display</div>}

        {/* list */}
        {!refreshing && (
          <ExpenseList expenses={expenses} setFormValues={setFormValues} />
        )}
      </div>
    </div>
  );
};

export default Expenses;
